.medicx-content{
    &-skill-wrap{
        @include max (991px){
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
    }
    &-bar{
        margin-bottom: 10px;
    }
    &-info{
        h6{
            text-align: left;
            color: var(--heading-color);
        }
    }
    &-progress-line{
        position: relative;
        height: 10px;
        width: 550px;
        background: var(--light-color);
        border-radius: 10px;
        transform: scaleX(0);
        transform-origin: left;
        animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        @include max (767px){
            width: 100%;
        }
        h6{
            height: 100%;
            position: absolute;
            border-radius: 10px;
            transform: scaleX(0);
            transform-origin: left;
            background: var(--accent-color);
            animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            
        }
        h6::before{
            position: absolute;
            content: "";
            height: 0;
            right: 0;
            top: -14px;
            width: 0;
            border: 7px solid transparent;
            border-bottom-width: 0px;
            border-right-width: 0px;
            border-top-style: #f0f0f0;
        }
        h6::after{
            position: absolute;
            right: -32px;
            top: -30px;
            color: var(--dark-bg);
            font-size: 20px;
            font-weight: 700;
            padding: 1px 8px;
            border-radius: 3px;
        }
        .html{
            width: 73%;
        }
        .html::after{
            content: "73%";
        }
        .css{
            width: 90%;
        }
        .css::after{
            content: "90%";
        }
    }
    h3{
        margin-bottom: 30px;
    }
    &-skill-wrap2{
        h4{
            margin-bottom: 30px;
        }
        @include max (991px){
