.medicx-section-title {
    h6{
        margin-bottom: 15px;
        letter-spacing: 0.005em;
        color: var(--accent-color);
    }
    p{
        font-size: 20px;
    }
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-630 {
            max-width: 630px;
        }
        &.max-width-700 {
            max-width: 700px;
        }
        &.max-width-750 {
            max-width: 750px;
        }
        &.max-width-780 {
            max-width: 780px;
        }
        &.max-width-820 {
            max-width: 820px;
        }
        &.max-width-large3 {
            max-width: 715px;
        }
        &.pb-50{
            padding-bottom: 50px;
            @include max (991px){
                padding-bottom: 30px;
            }
        }
    }
}
.max-width-630 {
    max-width: 630px;
}
.mb-0-767{
    @include max (991px){
        margin-bottom: 0;
    }
}
.pb-50{
    padding-bottom: 50px;
    @include max (991px){
        padding-bottom: 30px;
    }
}
