.medicx-section-padding2 {
    width: 100%;
}
.medicx-counter {
    &-wrap{
        display: flex;
        text-align: center;
        justify-content: space-between;
        gap: 30px;
        @include max (991px){
            flex-wrap: wrap;
        }
        @include max (767px){
            justify-content: center;
        }
    }
    &-data {
        h2 {
            color: var(--white-bg);
        }
        p {
            color: var(--white-bg);
        }
    }
    &-number{
        font-size: 70px;
        line-height: 1.07em; /* 116.667% */
        letter-spacing: -0.005em;
        font-weight: 700;
        margin-bottom: 15px;
        color: var(--white-color);
        @include max(1199px) { 
            font-size: 60px;
            letter-spacing: -3px;
        }
        @include max(991px) { 
            font-size: 48px;
            font-weight: 600;
        }
    
    }
    &-item {
        gap: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &-data2{
        width: 200px;
        text-align: left;
        p{
            color: var(--white-color);
            @include max(1199px) {
                font-size: 18px;
            }
        }
        .medicx-counter-number{
            margin-bottom: 5px;
        }
    }
}


// medicx project
.medicx-faq-section{
    position: relative;
    z-index: 1;
}
.medicx-counter{
    &--wrapper{
        text-align: center;
    }
    &-data{
        padding: 40px 65px;
        border-radius: 10px;
        text-align: center;
        background: var(--accent-color);
        @include max (991px){
            margin-bottom: 24px;
        }
        h2{
            margin-bottom: 15px;
        }
    }
}
