.pricing-text {
    ul {
        li{
            position: relative;
            padding-left: 35px;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: #0E0E0E; 
            margin-bottom: 15px;
            img {
                position: absolute;
                left: 0;
            }
        }
    }
}
// end pricing section v1

.medicx-iconbox-icon {
    flex-shrink: 0;
} 
.medicx-content-iconbox-wrap {
    display: flex;
    gap: 20px;
    max-width: 640px;
    margin-bottom: 24px;
    @include max(991px){
        gap: 15px;
        &:last-child{
            margin-bottom: 0;
        }
    }
    &.content-v5 {
        max-width: none;
        margin-bottom: 15px!important;
    }
}

// medicx content2 section
.medicx-list-icon {
    &-wrap {
        display: flex;
        gap: 20px;
        margin: 24px 0 0 0;
        @include max (991px){
            margin: 24px 0 0 0;
        }
        @include max (767px){
            display: block;
        }
    }
    &-wrap2{
        display: flex;
        gap: 24px;
        align-items: center;
        @include max (1199px){
            gap: 9px;
        }
        @include max (991px){
            gap: 24px;
        }
        @include max (767px){
            display: block;
        }
    }
    &-wrap3{
        display: flex;
        gap: 24px;
        @include max (991px){
            gap: 60px;
        }
        @include max (767px){
            margin-top: 40px;
            display: block;
        }
    }
    &-icon {
        ul{
            li{
                position: relative;
                font-size: 24px;
                font-weight: 700;
                line-height: 36px;
                text-align: left;
                color: var(--body-color);
                padding-left: 30px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                @include max(1199px) {
                    font-size: 20px;
                }
                @include max (767px){
                    margin-bottom: 10px;
                }
                img {
                    position: absolute;
                    left: 0;
                    top: 9px;
                }
                @include max(575px) {
                    font-size: 20px;
                }
            }
        }
        &.medicx-content1v2{
            width: 300px;
            @include max (767px){
                width: 100%;
            }
            ul{
                li{
                    position: relative;
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 32px;
                    text-align: left;
                    color: var(--body-color);
                    padding-left: 30px;
                    margin-bottom: 15px;
                    @include max (767px){
                        font-size: 19px;
                        margin-bottom: 12px;
                    }
                    img {
                        position: absolute;
                        left: 0;
                        top: 7px;
                    }
                }
            }
        }
    }
  
    &-icon2{
        ul{
            li{
                position: relative;
                font-size: 24px;
                font-weight: 700;
                line-height: 36px;
                text-align: left;
                color: var(--body-color);
                padding-left: 80px;
                margin-bottom: 15px;
                @include max (767px){
                    margin-bottom: 10px;
                }
                img {
                    position: absolute;
                    left: 0;
                    top: 9px;
                }
            }
        }
        &.icon3{
            ul{
                li{
                    margin-bottom: 24px;
                    @include max (1399px){
                        padding-left: 70px;
                    }
                    @include max (1199px){
                        font-size: 20px;
                        line-height: 28px;
                        padding-left: 44px;
                    }
                    @include max (991px){
                        font-size: 24px;
                        line-height: 36px;
                        padding-left: 80px;
                    }
                    @include max (767px){
                        margin-bottom: 30px;
                    }
                    @include max (575px){
                        padding-left: 65px;
                    }
                    img{
                        @include max (767px){
                            position: absolute;
                            left: 0;
                            top: -9px;
                        }
                        @include max (1199px){
                            width: 40px;
                        }
                        @include max (991px){
                            width: 20%;
                        }
                        @include max (767px){
                            width: 12%;
                        }
                        @include max (575px){
                            width: 10%;
                        }
                        @include max (575px){
                            top: 0;
                        }@include max (499px){
                            top: 15px;
                            width: 15%;
                        }
                    }
                }
            }
            
        }
    }
   
}



/* top scroll */
.progress-wrap {
	position: fixed;
	right: 30px;
	bottom: 30px;
	height: 50px;
	width: 50px;
	cursor: pointer;
	display: block;
	border-radius: 50px;
	box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.04);
	z-index: 10000;
	opacity: 0;
	visibility: hidden;
	transform: translateY(15px);
	-webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}
.progress-wrap.active-progress {
	opacity: 1;
	visibility: visible;
	transform: translateY(0);
}

.progress-wrap{
    svg path { 
        fill: none; 
    }
}
.progress-wrap svg.progress-circle path {
	stroke: var(--accent-color); /* --- Lijn progres kleur --- */
	stroke-width: 4;
	box-sizing:border-box;
	-webkit-transition: all 200ms linear;
    transition: all 200ms linear;
}

.top-arrow {
    top: 44%;
    left: 60%;
    position: absolute;
    transform: translate(-50%, -50%);
    img{
        width: 55%;
        transform: rotate(-90deg);
    }
}