header.medicx-header-section {
  position: fixed;
  z-index: 9;
  width: 100%;
  top: 0;
  transition: all 0.4s;
  background: var(--white-color);
}
header.medicx-header-section.sticky-menu{
  box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
  background-color: var(--white-bg);
}

.medicx-header-top, 
.medicx-header-top2, 
.medicx-header-top3{
  transition: all 0.4s;
}
.sticky-menu{
  .medicx-header-top{
    margin-top: -65px;
    @include max (1199px){
      margin-top: -112px;
    }
  }
  .medicx-header-top2{
    margin-top: -92px;
  }
  .medicx-header-top3{
    margin-top: -81px;
    @include max (1199px){
      margin-top: -115px;
    }
  }
}

.white-color{
  color: var(--white-color)!important;
}





// medicx project

.medicx-header{
  &-top{
    background: var(--accent-color);
    padding: 15px 0;
    @include max (991px){
      display: none;
    }
  }
  &-top2{
    background: var(--white-color);
    padding: 15px 0;
    position: relative;
    z-index: 1;
    @include max (991px){
      display: none;
    }
  }
  &-top3{
    background: var(--white-color);
    padding: 15px 0;
    position: relative;
    z-index: 1;
    @include max (991px){
      display: none;
    }
  }
  
}
.medicx-header{
  &-bottom {
    padding: 10px 0;
    position: relative;
    z-index: 1;
    background: var(--white-color);
  }
  &-bottom2 {
    padding: 10px 0;
    background: var(--accent-color);
  }
  &-bottom3 {
    padding: 10px 0;
    position: relative;
    z-index: 1;
    background: var(--heading-color);
  }
}
.medicx-header-info{
  &-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include max (1199px){
      display: block;
      text-align: center;
    }
    &.pl-20{
      padding-left: 18px;
    }
  }
  
  &-data{
    ul{
      li{
        display: inline-block;
        margin-right: 20px;
        font-size: 16px;
        font-weight: 700;
        color: var(--white-color);
        img{
          margin-right: 10px;
        }
        @include max (767px){
          margin-bottom: 10px;
        }
        a{
          margin-right: 20px;
          color: var(--white-color);
         
          img{
            margin-right: 10px;
          }
        }
      }
    }
  }
  // End v1
  &-column{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-wraper{
    display: flex;
    gap: 40px;
  }
  &-item{
    display: flex;
    gap: 12px;
    align-items: center;
  }
  &-icon2{
    flex-shrink: 0;
    img{
      width: 30px;
      @include max (1199px){
        width: 25px;
      }
    }
  }
  &-data2{
    p{
      font-size: 16px;
      margin-bottom: 5px;
      line-height: 24px;
    }
    h6{
      font-size: 18px;
    }
  }
  

  &-data3{
    ul{
      li{
        font-size: 16px;
        font-weight: 700;
        margin-right: 24px;
        display: inline-block;
        color: var(--heading-color);
        &:last-child{
          margin-right: 0;
        }
        img{
          margin-right: 10px;
        }
        @include max (767px){
          margin-bottom: 10px;
        }
        a{
          font-weight: 700;
          color: var(--heading-color);
          img{
            margin-right: 10px;
          }
        }
      }
    }
  }
  
}

.brand-logo{
  @include max (767px){
    img{
      width: 80%;
    }
  }
}

.brand-logo2{
  display: none;
  @include max (991px){
    display: block;
  }
  @include max (767px){
    img{
      width: 80%;
    }
  }
}

.brand-logo3{
  padding: 0 50px;
  top: -82px;
  left: -25%;
  display: flex;
  height: 159px;
  align-items: center;
  position: absolute;
  transition: all 0.4s;
  background: var(--accent-color);

  @include max (1700px){
    position: static;
    padding: 0 30px;
    height: 80px;
    background: none;
  }
  @include max(1199px){
    padding: 20px;
    width: 180px;
    height: auto;
    margin-bottom: 5px;
  }
  @include max (767px){
    padding: 0;
    background: transparent;
    img{
      width: 80%;
    }
  }
}

.sticky-menu{
  .brand-logo3 {
    padding-top: 85px;
    @include max (1700px){
      padding-top: 20px;
    }
    @include max (767px){
      padding-top: 0;
    }
  }
}




