.medicx-team-member{
    &-wrap{
        position: relative;
        display: block;
        border-radius: 20px;
        transition: all 0.4s;
        border: 1px solid var(--light-color);
        margin-bottom: 24px;
        &:hover{
            border: 1px solid var(--white-color);
            box-shadow: 0px 4px 80px 0px #00000012;
            .medicx-team-member-img img.team-thumb{
                transform: scale(1.1);
            }
        }
        &:hover .medicx-team-member-img::before{
            opacity: 1;
        }
        &:hover .social_position{
            opacity: 1;
            z-index: 99;
            bottom: 30px;
            visibility: visible;
        }
    }
    &-img {
        position: relative;
        overflow: hidden;
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            background: linear-gradient(180deg, rgba(3, 3, 3, 0) 32.5%, rgba(3, 3, 3, 0.8) 100%);
            opacity: 0;
            transition: .3s;
        }
        img{
            width: 100%;
            object-fit: cover;
            transition: all .4s;
        }
      
    }
    &-content{
        padding: 30px;
        h5{
            margin-bottom: 5px;
        }
        p{
            font-size: 18px;
        }
    }
}


.medicx-team{
    &-wrap{
        display: block;
        position: relative;
        h5,p{
            color: var(--white-color);
        }
        .team-position {
            transition: all .4s;
            position: absolute;
            bottom: 0;
            right: 0;
            opacity: 0;
            visibility: hidden;
            padding: 15px 25px;
            border-radius: 10px 0 0 10px;
            background-color: var(--accent-color);
            h4{
                margin-bottom: 15px;
            }
        }
        .team2-position {
            transition: all .4s;
            position: absolute;
            top: 10px;
            left: -24px;
            opacity: 0;
            visibility: hidden;
            padding: 15px 25px;
            border-radius: 10px;
            h4{
                margin-bottom: 15px;
            }
            ul{
                li{
                    display: block;
                    margin-bottom: 20px;
                    margin-right: 0;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    a{
                        border: 1px solid var(--heading-color);
                        &:hover{
                            svg path{
                                fill: white;
                            }
                        }
                    }
                }
            }
        }
        &:hover{
            .medicx-team-thumb::before {
                opacity: 1;
            }
            .team-position {
                opacity: 1;
                visibility: visible;
                bottom: 24px;
                right: 0;
            }
            .team2-position {
                opacity: 1;
                left: 0;
                visibility: visible;
            }
            .medicx-team-thumb img{
                transform: scale(1.1);
            }
        } 
    }
    &-thumb{
        margin-bottom: 24px;
        position: relative;
        text-align: center;
        overflow: hidden;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            transition: .3s;
        }
        img{
            border-radius: 10px;
            width: 100%;
            object-fit: cover;
            transition: all .4s;
        }
    }
    &-wrapv3{
        padding: 20px 20px 0 20px;
        border-radius: 10px;
        background: var(--white-color);
        position: relative;
        display: block;
        margin-bottom: 24px;
        box-shadow: 0px 4px 80px 0px #00000012;
        .team3-position {
            transition: all .4s;
            position: absolute;
            bottom: 30%;
            left: 20px;
            opacity: 0;
            visibility: hidden;
            padding: 15px;
            border-radius: 10px;
            background: var(--accent-color);
            border-radius: 0 10px 10px 0;
            h4{
                margin-bottom: 15px;
            }
            ul{
                li{
                    display: inline-block;
                    margin-right: 15px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    a{
                        width: 30px;
                        height: 30px;
                        display: flex;
                        align-items: center;
                        border-radius: 100px;
                        transition: all 0.4s;
                        justify-content: center;
                        border: 1px solid var(--white-color);
                        &:hover{
                            background: var(--white-color);
                            border: 1px solid var(--white-color);
                            svg path{
                                fill: var(--accent-color);
                            }
                        }
                    }
                }
            }
        }
        &:hover{
            .medicx-team-button-wrapper{
                a .team-btnv3{
                    background: var(--accent-color);
                    border-radius: 0 0 0 10px;

                }
                a .medicx-team-button-icon{
                    svg path{
                        stroke: var(--white-color);
                    }
                    &.single-team{
                        svg path{
                            stroke: var(--heading-color);
                        }
                    }
                }
                a .medicx-team-button-icon2{
                    svg path{
                        stroke: var(--heading-color);
                    }
                }
                a .medicx-team-button-authore{
                    p{
                        font-size: 16px!important;
                        line-height: 28px;
                        color: var(--white-color);
                    }
                    &.single-team{
                        p{
                            color: var(--heading-color)!important;
                        }
                    }
                }
                a  .medicx-team-button-authore2{
                    color: var(--heading-color);
                }
            }
            .team3-position {
                opacity: 1;
                visibility: visible;
                bottom: 34%;
            }
            .medicx-team-member img{
                transform: scale(1.1);
            }
        }
    }
    &-member{
        position: relative;
        text-align: center;
        overflow: hidden;
        img{
            width: 100%;
            object-fit: cover;
            transition: all .4s;
        }
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            transition: .3s;
        }
    }
    :hover .team3-position {
        opacity: 1;
        visibility: visible;
    }
    &-member-authore{
        padding: 24px 0 74px 0;
        p{
            font-size: 16px!important;
            line-height: 28px;
            color: var(--body-color);
        }
        @include max (991px){
            padding: 24px 0 60px 0;
        }
    }
    &-button-wrapper{
        display: flex;
        margin-top: -51px;
        margin-left: -21px;
        @include max (991px){
            flex-wrap: wrap;
        }
        a .medicx-team-button-wrap{
            .medicx-team-button-authore{
                p{
                    transition: all 0.4s;
                    font-weight: 700;
                    color: var(--heading-color);
                    font-size: 16px;
                    line-height: 28px;
                }
            }
        }
        
    }
    &-button-wrap {
        display: flex;
        gap: 10px;
        padding: 10px 44px;
        align-items: center;
        transition: all .4s;
        border-radius: 0 0 0 10px;
        @include max (1399px){
            padding: 10px 31px;
        }
        @include max (1399px){
            padding: 10px 56px;
        }
        @include max (991px){
            padding: 10px 20px;
        }
    }
    &-button-icon{
        svg{
            transition: all 0.4s;
            margin-bottom: 5px;
        }
    }
}
.border1px{
    border-radius: 0 0 10px 0;
    border: 1px solid var(--light-color2);
    @include max (991px){
        margin-right: -184px;
        justify-content: center;
    }
    @include max (767px){
        margin-right: -198px;
    }
    &:hover{
        background: var(--white-color);
        border-radius: 0 0 10px 0;
    }
}

 .border1px .medicx-team-button-authore2 {
    p{
        color: var(--heading-color);
        font-weight: 700;
        font-size: 16px;
        line-height: 28px;
    }
}
.medicx-member{
    &-skill-wrap2{
        h4{
            margin-bottom: 24px;
        }
        @include max (991px){
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
    }
    &-bar2{
        margin-bottom: 10px;
    }
    &-info2{
        h6{
            text-align: left;
            color: var(--heading-color);
        }
    }
    &-progress-line2{
        position: relative;
        height: 6px;
        background: rgba(0, 34, 255, 0.05)!important;
        border-radius: 10px;
        transform: scaleX(0);
        transform-origin: left;
        animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
        margin-bottom: 20px;
        @include max (991px){
            width: 550px;
        }
        &:last-child{
            margin-bottom: 0;
        }
        @include max (767px){
            width: 100%;
        }
        h6{
            height: 100%;
            position: absolute;
            border-radius: 10px;
            transform: scaleX(0);
            transform-origin: left;
            background: var(--accent-color);
            animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            
        }
        h6::before{
            position: absolute;
            content: "";
            height: 0;
            right: 0;
            top: -14px;
            width: 0;
            border: 7px solid transparent;
            border-bottom-width: 0px;
            border-right-width: 0px;
            border-top-style: #f0f0f0;
        }
        h6::after{
            position: absolute;
            right: -32px;
            top: -30px;
            color: var(--dark-bg);
            font-size: 20px;
            font-weight: 700;
            padding: 1px 8px;
            border-radius: 3px;
        }
        .html1{
            width: 83%;
        }
        .html1::after{
            content: "83%";
        }
        .html2{
            width: 90%;
        }
        .html2::after{
            content: "90%";
        }
        .html3{
            width: 75%;
        }
        .html3::after{
            content: "75%";
        }
    }
   
}
@keyframes animate {
    100% {
    transform: scaleX(1);
    }
}


// inner page single-team


.team-member-content{
    h6{
        color: var(--heading-color);
    }
}
.p-130{
    padding: 130px 0;
    @include max(991px) {
        padding: 90px 0;
    }
    @include max(767px) {
        padding: 60px 0;
    }
}
.medicx-member-experience{
    h4{
        margin-bottom: 24px;
    }
    ul{
        li{
            font-weight: 600;
            font-size: 20px;
            line-height: 32px;
            margin-bottom: 5px;
        }
    }
}


.medicx-team-details-thumb{
    text-align: center;
    img{
        width: 100%;
        @include max (991px){
            margin-bottom: 30px;
        }
    }
}
.medicx-member-field-experience{
    &-wrap{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include max (991px){
            display: block;
        }
    }
    &-title{
        h4{
            margin-bottom: 50px;
            @include max (991px){
                margin-bottom: 40px;
            }
            @include max (767px){
                margin-bottom: 30px;
            }
        }
        h5{
            margin-bottom: 24px;
        }
        span{
            font-weight: 400;
            color: var(--body-color);
        }
    }
    &-info{
        padding: 30px;
        background: rgba(0, 34, 255, 0.05);
        h5{
            margin-bottom: 24px;
        }
        @include max (991px){
            margin-top: 50px;
            max-width: 90%;
        }
        @include max (767px){
            margin-top: 50px;
            max-width: 100%;
        }
    }
}
.team-contact-data{
    ul{
        li{
            font-weight: 700;
            color: var(--heading-color);
            margin-bottom: 15px;
            img{
                margin-right: 15px;
            }
            @include max (991px){
                margin-bottom: 24px;
            }
            a{
                color: var(--heading-color);
                margin-bottom: 15px;
                img{
                    margin-right: 15px;
                }
                
            }
        }
    }
}