

// strat medicx project


.medicx-hero-section {
    margin-top: 132px;
    @include max(1199px) {
      margin-top: 0;
    }
}
.medicx-hero{
  &-item{
    width: 100%;
    z-index: 0;
    display: inline-block;
    padding: 150px 0;
    opacity: 1!important;
    position: relative;
    background-size: cover;
    @include max (1199px){
      padding: 300px 0 150px;
    }
    @include max (991px){
      padding: 200px 0 100px;
    }
    @include max (767px){
      padding: 150px 0 80px;
    }

    &::before{
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background: linear-gradient(90deg, #030303 0%, rgba(3, 3, 3, 0) 68.5%);
    }
    &.medicx-herov3{
      padding: 150px 0 250px!important;
      @include max (1199px){
        padding: 320px 0 150px!important;
      }
      @include max (991px){
        padding: 200px 0 100px!important;
      }
      @include max (767px){
        padding: 150px 0 80px!important;
      }
    }
  }

  &-item2{
    width: 100%;
    z-index: 0;
    display: inline-block;
    padding: 150px 0 270px;
    opacity: 1!important;
    position: relative;
    background-size: cover;
    @include max (1199px){
      padding: 300px 0 250px;
    }
    @include max (991px){
      padding: 200px 0 250px;
    }
    @include max (767px){
      padding: 150px 0 220px;
    }
    &::before{
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background: var(--accent-color);
      opacity: 50%;
      mix-blend-mode: multiply;
    }
  }
}
.medicx-hero-slider{
  &-content {
    max-width: 900px;
    @include max(1199px) {
      margin: 0 auto;
      max-width: 800px;
      text-align: center;
    }
    h6{
      color: var(--white-color);
      margin-bottom: 20px;
    }
    p{
        font-size: 24px;
        line-height: 36px;
        color: var(--white-color);
        @include max(991px) {
          font-size: 20px;
        }
    }
    &.center{
      max-width: 1200px;
      margin: 0 auto;
      @include max(1399px) {
        max-width: 940px;
      }
      @include max(1199px) {
        max-width: 800px;
      }
    }
   
  }

}

.medicx-hero-btn{
  &-wrap {
    display: flex;
    align-items: center;
    gap: 24px;
    flex-wrap: wrap;
    position: relative;
    &.center{
      justify-content: center;
    }
    @include max(1199px) {
      justify-content: center;
    }
  }

  
}

.medicx-hero-slider-init .slick-slide {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
}

.medicx-hero-slider-init2 ul.slick-dots {
  position: absolute;
  bottom: 50%;
  right: 6%;
  li{
    margin-bottom: 24px;
    transition: all .4s;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.medicx-hero-slider-init2 .slick-dots li.slick-active {
  width: 30px;
  height: 30px;
  border: 1px solid var(--white-color);
  border-radius: 100px;
  @include max (767px){
    display: none;
  }
}

.medicx-hero-slider-init2 .slick-dots button {
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 100px;
  padding: 0;
  border: none;
  background-color: var(--white-color);
  text-indent: -9999px;
  transition: .3s;
  display: flex;
  @include max (767px){
    display: none;
  }
}
.medicx-left-arrowbtn {
    position: absolute;
    top: 55%;
    left: 5%;
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100px;
    img{
        position: absolute;
        top: 22%;
        right: 40%;
        width: 15px;
        cursor: pointer;
    }
}
.medicx-right-arrowbtn {
    position: absolute;
    top: 55%;
    right: 5%;
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100px;
    img{
        position: absolute;
        top: 24%;
        right: 30%;
        width: 15px;
        cursor: pointer;
    }
}



.medicx-hero-slider-init .medicx-hero-next, 
.medicx-hero-slider-init .medicx-hero-prev {
  top: 50%;
  left: 50px;
  width: 80px;
  height: 80px;
  z-index: 1;
  border: none;
  display: flex;
  cursor: pointer;
  position: absolute;
  transition: all .4s;
  align-items: center;
  border-radius: 100px;
  background-size: 20px;
  justify-content: center;
  transform: translateY(-50%);
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--white-color);
  background-image: url(../images/v1/leftarrow.svg);
  @include max (1450px){
    width: 65px;
    height: 65px;
    left: 20px;
    background-size: 15px;
  }
  @include max (991px){
    display: none!important;
  }
}
.medicx-hero-slider-init .medicx-hero-prev {
  left: auto;
  right: 50px;
  background-image: url(../images/v1/rightarrow.svg);
  @include max (1450px){
    right: 20px;
  }

  @include max (767px){
    display: none!important;
  }
}

.slider-custom-anim-left.slider-animated {
    --animation-name: custom-anim-left!important;
  }
.slider-custom-anim-left {
  opacity: 0;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-duration: 1s;
  animation-delay: 0.3s;
  animation-name: var(--animation-name);
}

.slider-animated {
    opacity: 1;
  }

  .custom-anim-left {
    animation: custom-anim-left 1.3s forwards cubic-bezier(0.645, 0.045, 0.355, 1) 0.4s;
    opacity: 0;
  }
  
  @keyframes custom-anim-left {
    0% {
      transform: translateX(-5%);
      clip-path: inset(0 100% 0 0);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      clip-path: inset(0 0 0 0);
      opacity: 1;
    }
  }


  [data-animation-in] {
    opacity: 0;
    -webkit-animation-duration: 2s;
    animation-duration: 1s;
    -webkit-transition: opacity 0.5s ease 0.3s;
    transition: opacity 0.5s ease 0.3s;
  }
  .medicx-hero-slider-content .animated {
    transition: all 0.5s ease;
    opacity: 1;
  }

  .medicx-divider{
    height: 1px;
    width: 100%;
    background-color: var(--light-color2);
  }

  //  hero3

.medicx-hero-slider-init3 ul.slick-dots {
  bottom: 15%;
  right: 40%;
  position: absolute;
  display: flex;
  gap: 24px;
  li{
    transition: all .4s;
  }
  @include max (1450px){
    right: 30%;
  }
  @include max (1199px){
    bottom: 8%;
  }
  @include max (991px){
    right: 24%;
    bottom: 8%;
  }
  @include max (767px){
    display: none!important;
  }
}
.medicx-hero-slider-init3 .slick-dots li.slick-active {
  background: var(--white-color);
  border-radius: 10px;
}

.medicx-hero-slider-init3 .slick-dots button {
  display: block;
  padding: 0;
  border: none;
  text-indent: -9999px;
  transition: .3s;
  width: 80px;
  height: 5px;
  background: var(--white-color);
  opacity: .2;
  border-radius: 10px;
}
