.medicx-social{
    &-icon{
        &.social_position{
            transition: all .4s;
            position: absolute;
            bottom: 0;
            text-align: center;
            width: 100%;
            opacity: 0;
            visibility: hidden;
            
        }
        &.team-v1{
            ul{
                li{
                    a{
                        width: 50px;
                        height: 50px;
                    }
                }
            }
        }
        &.footer-1-social-icon{
            ul{
                li{
                    margin-right: 15px;
                    &:last-child{
                        margin-right: 0;
                    }
                    a{
                        width: 40px;
                        height: 40px;
                    }
                }
            }
        }
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    width: 30px;
                    height: 30px;
                    border-radius: 100px;
                    border: 1px solid var(--white-color);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all 0.4s;
                    &:hover{
                        background: var(--accent-color);
                        border: transparent;
                    }
                    img{
                        width: auto;
                        margin: 0;
                    }
                }
            }
        }

    }
    &-icon2{
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    width: 40px;
                    height: 40px;
                    border-radius: 100px;
                    border: 1px solid var(--heading-color);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all 0.4s;
                    &:hover{
                        background: var(--accent-color);
                        border: transparent;
                    }
                    &:hover{
                        border: 1px solid var(--accent-color);
                        svg path{
                            fill: white;
                        }
                    }
                    img{
                        width: auto;
                    }
                }
            }
        }
    }
}

.medicx-social-icon-wrap{
    .medicx-footer3{
        ul{
            li{
                a{
                    border: 1px solid var(--heading-color);
                    &:hover{
                        border: 1px solid var(--accent-color);
                        svg path{
                            fill: white;
                        }
                    }
                }
            }
        }
    }
}

.medicx-footer-social{
    &-wrap {
        display: grid;
        padding-bottom: 80px;
        grid-template-columns: repeat(5, 1fr);
        @include max (767px){
            gap: 10px;
            padding-bottom: 60px;  
            grid-template-columns: repeat(2, 1fr);          
        }
    }
    &-button{
        font-size: 16px;
        line-height: 28px;
        font-weight: 400;
        display: flex;
        gap: 10px;
        padding: 15px;
        align-items: center;
        color: var(--white-color);
        justify-content: center;
        transition: all 0.4s;
        border: 1px solid var(--accent-color);
        border-right: 0;
        &:first-child{
            border-radius: 50px 0 0 50px;
            @include max(767px) {
                border-radius: 0;
            }
        }
        &:last-child{
            border-radius: 0px 50px 50px 0px;
            border-right: 1px solid var(--accent-color);
            @include max(767px) {
                border-radius: 0;
            }
        }
        &:hover{
            color: var(--white-color);
            background-color: var(--accent-color);
        }
        @include max(767px) {
            border: 1px solid var(--accent-color)!important;
        }
    }

}

.center{
    text-align: center;
    justify-content: center;
}
//  header social

.medicx-social-icon{
    &-wrap{
      @include max (1199px){
        margin-top: 15px;
      }
    }
    &-img{
        ul {
            li{
                display: inline-block;
                @include max (767px){
                  margin-bottom: 20px;
                }
                a{
                  margin-right: 30px;
                }
            }
        }
    }
  }