// frist checkbox

.checkbox-wrapper-item [type="radio"]:checked,
.checkbox-wrapper-item [type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
}
.checkbox-wrapper-item [type="radio"]:checked + label,
.checkbox-wrapper-item [type="radio"]:not(:checked) + label
{
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    line-height: 20px;
    display: inline-block;
    color: var(--body-color);
    font-size: 20px;
  
}

.checkbox-wrapper-item [type="radio"]:checked + label:before,
.checkbox-wrapper-item [type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    border: 1px solid var(--accent-color);
    border-radius: 100%;
    background: #fff;
}
.checkbox-wrapper-item [type="radio"]:checked + label:after,
.checkbox-wrapper-item [type="radio"]:not(:checked) + label:after {
    content: '';
    width: 20px;
    height: 20px;
    background: var(--accent-color);
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 100%;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}
.checkbox-wrapper-item [type="radio"]:not(:checked) + label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}

