

.medicx-testimonial {
  &-content {
    width: 415px;
    border: 1px solid var(--light-color);
    padding: 24px;
    border-radius: 10px;
    margin-bottom: 24px;
  }
  &-rating {
    margin-bottom: 20px;
    ul {
      li {
          display: inline-block;
      }
    }
    @include max (991px) {
      margin-bottom: 20px;
    }
  }
  &-data{
    margin-bottom: 40px;
    p {
      font-size: 20px;
      line-height: 32px;
      font-weight: 600;
      color: var(--heading-color);
    }
  } 
  &-author {
    display: flex;
    gap: 24px;
  }
  &-author-data{
    p{
      font-size: 16px;
      line-height: 28px;
    }
  }
  &-datav3{
    padding-bottom: 24px;
    p{
      font-size: 24px;
      line-height: 36px;
      font-weight: 600;
      color: var(--heading-color);
      @include max (575px){
        font-size: 20px;
      }
    }
  }
}
.medicx-testimonialv2-slider-init .medicx-testimonial-slider-content{
  &-wrap{
    text-align: center!important;
    max-width: 750px;
    margin: 0 auto;
    display: block!important;
  }
  &-icon{
    display: flex;
    justify-content: center;
    margin-bottom: 24px;
  }
  &-data{
    h5{
      @include max (575px){
        font-size: 20px;
      }
    }
  }
}
.medicx-testimonialv2{
  justify-content: center;
  margin-top: 30px;
  .medicx-testimonial-author-data{
    text-align: start;
  }
}
.medicx-testimonial-slider-init3 .slick-slide{
  margin-right: 24px;
}

// jihad add
.medicx_screenfix_right {
  margin-right: calc((-100vw + 100%) / 2);
  max-width: 1610px;
}


.medicx-testimonial-slider-init .medicx-t-next, 
.medicx-testimonial-slider-init .medicx-t-prev {
  bottom: -33%;
  left: 41%;
  width: 60px;
  height: 60px;
  z-index: 99;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  background-size: 16px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  justify-content: center;
  background-image: url(../images/v1/leftarrow.svg);
  border: 1px solid var(--heading-color);
  background-color: var(--white-color);
  border-radius: 100px;
  transform: translateY(-50%);

  @include max (1450px){
    width: 55px;
    height: 55px;
    left: 45%;
    background-size: 15px;
  }
  @include max (1199px){
    left: 44%;
    bottom: -20%;
  }
  @include max (991px){
    left: 42%;
  }
  @include max (767px){
    display: none!important;
  }
}
.medicx-testimonial-slider-init .medicx-t-prev {
  left: auto;
  right: 50%;
  background-image: url(../images/v1/rightarrow.svg);
  @include max (1450px){
    right: 44%;
  }
  @include max (1199px){
    right: 43%;
  }
  @include max (991px){
    right: 41%;
  }
  @include max (767px){
    display: none!important;
  }
}
.medicx-testimonial-slider-init .slick-slide{
  margin-right: 24px;
  @include max(575px) {
    margin-right: 0;
  }
}
.medicx-testimonial-slider-init .slick-list {
  padding-left: 0px!important; // half partial
}


// v2

.medicx-testimonialv2-slider-init .medicx-t-next, 
.medicx-testimonialv2-slider-init .medicx-t-prev {
  top: 50%;
  left: 0;
  width: 80px;
  height: 80px;
  z-index: 99;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  background-size: 20px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  justify-content: center;
  background-image: url(../images/v1/leftarrow.svg);
  border: none;
  border-radius: 100px;
  transform: translateY(-50%);
  opacity: .2;
  &:hover{
    opacity: 1;
  }
  @include max (1450px){
    width: 65px;
    height: 65px;
    left: 20px;
    background-size: 15px;
  }
  @include max (991px){
    display: none!important;
  }
}
.medicx-testimonialv2-slider-init .medicx-t-prev {
  left: auto;
  right: 0;
  background-image: url(../images/v1/rightarrow.svg);
  opacity: .2;
  &:hover{
    opacity: 1;
  }
  @include max (1450px){
    right: 20px;
  }
  @include max (991){
    display: none!important;
  }
}
//  medicx v3

.medicx-testimonial-slider-init3 ul.slick-dots {
  bottom: -30%;
  right: 45%;
  position: absolute;
  display: flex;
  gap: 8px;
  @include max (1199px){
    gap: 15px;
  }
  li{
    transition: all .4s;
  }
  @include max (1450px){
    right: 44%;
  }
  @include max (1199px){
    right: 40%;
        bottom: -25%;
  }
  @include max (991px){
    right: 35%;
    bottom: -15%;
  }
  @include max (767px){
    display: none!important;
  }
}
.medicx-testimonial-slider-init3 .slick-dots li.slick-active {
  background: var(--accent-color);
  border-radius: 10px;
}

.medicx-testimonial-slider-init3 .slick-dots button {
  display: block;
  padding: 0;
  border: none;
  text-indent: -9999px;
  transition: .3s;
  width: 45px;
  height: 5px;
  background: rgba(0, 34, 255, 0.2);
  border-radius: 10px;
}
