.medicx-footer-top {
    padding: 130px 0 90px;
    @include max (991px){
        padding: 100px 0 70px;
    }
    @include max (767px){
        padding: 60px 0 50px;
    }
  }
  .medicx-textarea{
    img{
        margin-bottom: 24px;
    }
    p{
        font-size: 18px;
        color: var(--white-color);
    }
    a.tel-number{
        h4{
            color: var(--white-color);
        }
    }
    @include max(1199px) {
        max-width: 600px;
    }
  }

  .medicx-footer-bottom{
    display: flex;
    padding: 35px 0;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #333;
    @include max(767px) {
        flex-wrap: wrap;
        gap: 20px;
        justify-content: center;
    }
  }

  .medicx-copywright{
    p{
        font-size: 18px;
        color: var(--white-color);
    }
  }



.ml-15{
    margin-left: 15px;
    @include max(991px) {
        margin-left: 0;
    }
}
.ml-26{
    margin-left: 26px;
    @include max(1199px) {
        margin-left: 0;
    }
}
.ml-60{
    margin-left: 60px;
    @include max(1199px) {
        margin-left: 0;
    }
}
.fs-22{
    h5{
        font-size: 22px!important;
    }
}
 .mr-25{
    margin-right: 25px;
    @include max(991px) {
        margin-right: 0;
    }
 }
 .ml-10{
    margin-left: -10px;
    @include max(1199px) {
        margin-left: -15px;
    }
    @include max(991px) {
        margin-left: -32px;
    }
    @include max(767px) {
        margin-left: -0;   
    }
 }
.mt-40{
    margin-top: 40px;
    @include max(991px) {
        margin-top: 30px;   
    }
}
.ml-32{
    margin-left: 30px;   
    @include max (1199px){
        margin-left: -20px;
    }
    @include max (991px){
        margin-left: -35px;
    }
    @include max (767px){
        margin-left: 0;
    }
}
.medicx-footer{
    &-menu{
        h5 {
            margin-bottom: 24px;
            color: var(--white-color);
        }
        ul {
            li{
                color: var(--white-color);
                margin-bottom: 12px;
                font-size: 18px;
                &:last-child{
                    margin-bottom: 0;
                }
         
                a{
                    position: relative;
                    transition: all 0.4s;
                    color: var(--white-color);
                    font-size: 18px;
                    &::before{
                        content: " ";
                        left: 0;
                        bottom: 0;
                        width: 0;
                        height: 1px;
                        position: absolute;
                        transition: all .4s;
                        background: var(--white-color);
                        
                    }
                    &:hover{
                        &::before{
                            width: 100%;
                        }
                    }
                   
                    img{
                        margin-right: 10px;
                    }
                }
            }
        }
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (991px){
            margin-top: 30px;
        }
        &.footer-menu-inline{
            ul{
                li{
                    display: inline-block;
                    margin-right: 40px;
                    margin-bottom: 0;
                    @include max(991px) {
                        margin-right: 20px;
                    }
                    &:last-child{
                        margin-right: 0;
                    }
                }
            }
        }
    
    }
    &-menu2{
        h5 {
            margin-bottom: 24px;
            color: var(--white-color);
        }
        ul {
            li{
                color: var(--white-color);
                margin-bottom: 12px;
                font-size: 18px;
                &:last-child{
                    margin-bottom: 0;
                }
         
                a{
                    position: relative;
                    transition: all 0.4s;
                    color: var(--white-color);
                    font-size: 18px;
                    img{
                        margin-right: 10px;
                    }
                }
            }
        }
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (991px){
            margin-top: 30px;
        }
       
    
    }
}
.footer1-contact{
    &-data{
        @include max (1199px){
            margin-top: 30px;
        }
        h5{
            margin-bottom: 24px;
        }
        ul{
            li{
                margin-bottom: 15px;
                color: var(--white-color);
                &.location{
                    position: relative;
                    padding-left: 40px;
                }
                
                img{
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                a{
                    position: relative;
                    padding-left: 40px;
                    color: var(--white-color);
                    @include max (991px){
                        padding-left: 35px;
                    }
                    img{
                        position: absolute;
                        top: 6px;
                        left: 0;
                    }
                }
            }
        }
    }
}


.medicx-footer3 {
    &-wrap {
        display: flex;
        justify-content: space-between;
        padding: 30px 0;
        border-top: 1px solid var(--body-color);
        @include max (767px){
            display: block;
            text-align: center;
        }
    }
    &-text{
        align-items: center;
        display: flex;
        @include max (767px){
            display: block;
            margin: 0 0 30px 0;
        }
    }
    p {
        line-height: 28px;
        color: var(--grey-color);
    }
}
// end medicx footer 1
.medicx-subscription-field2  {
    input{
       margin: 0;
       height: 50px!important;
       width: 100%;
       color: var(--white-color);
       font-weight: 600;
       font-size: 16px;
       border-radius: 50px;
       background-color: var(--heading-color)!important;
       padding: 15px 28px !important;
       transition: all .4s;
       letter-spacing: .5px;
       border: 1px solid var(--body-color) !important;
       &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: var(--white-color);
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color: var(--white-color);
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color: var(--white-color);
        }
        &:-moz-placeholder { /* Firefox 18- */
          color: var(--white-color);
        }
        &:focus{
            border: 1px solid var(--white-color)!important;
        }
    }
    
  }