
.medicx-features{
    &-wrapper{
        padding: 30px;
        text-align: center;
        margin-bottom: 24px;
        border-radius: 10px;
        transition: all 0.4s;
        border: 1px solid var(--light-color);
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-data{
        h4{
            margin-bottom: 15px;
        }
        p{
            font-size: 18px;
        }
    }
    &-icon{
        margin-bottom: 24px;
    }
}
