.medicx-video-thumb .medicx-popup-video {
  top: 49%;
  left: 56.5%;
  position: absolute;
  transform: translate(-50%, -50%);
  @include max (1199px){
    left: 62%;
  }
    img{
      width: 40%;
      padding: 8px;
      border-radius: 100px;
      background: var(--white-bg);
      animation: animName 10s linear infinite;
      @include max (1199px){
        width: 35%;
        left: 62%;
      }
      @include max (767px){
        width: 30%;
      }
    }
}
.medicx-popup-video {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}



.medicx-video{
  &-section{
    position: relative;
    @include max (575px){
      img.video-thumb-v3{
        height: 180px;
        width: 100%;
      }
    }
    &::before {
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      background: rgba(3, 3, 3, 0.5);
    }
    .medicx-popup-video{
      width: 150px;
      padding: 10px;
      position: absolute;
      top: 37%;
      left: 46%;
      border-radius: 100px;
      background-color: var(--white-bg);
      animation: animName 10s linear infinite;
      @include max (991px){
        width: 100px;
        top: 33%;
        left: 45%;
      }
      @include max (575px){
        top: 28%;
        left: 41%;
        padding: 5px;
      }
    }
    .play-btn{
      img{
        position: absolute;
        top: 49%;
        left: 50%;
        transform: translate(-50%, -50%);
        @include max (1700px){
          left: 50.5%;
          top: 51%;
        }
        @include max (1399px){
          left: 51.5%;
          top: 54%;
        }
        @include max (1199px){
          left: 54%;
          top: 61%;
        }
        @include max (991px){
          left: 51.5%;
          top: 53%;
          width: 15px;
        }
        @include max (767px){
          left: 52%;
          top: 55%;
          width: 20px;
        }
        @include max (575px){
          left: 55%;
        }
      }
    }
  }
}


.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background: var(--white-bg);
    opacity: 0;
    border-radius: 100%;
    left: -55px;
    top: -55px;
    z-index: 0;
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    @include max(991px) {
      width: 160px;
      height: 160px;
      left: -30px;
      top: -30px;
    }
    &.wave-1 {
      -webkit-animation-delay: 0s;
      animation-delay: 0s;
    }
    &.wave-2 {
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
    &.wave-3 {
      -webkit-animation-delay: 2s;
      animation-delay: 2s;
    }
  }
  
  
  @keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }
  
    50% {
        opacity: 0.9;
    }
  
    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
  }